import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class ApiFiveService {

  apiUrl = "https://raw.githubusercontent.com/samayo/country-json/master/src/country-by-capital-city.json"
  constructor(private _http:HttpClient) { }

  getAllUser(){
   return this._http.get(this.apiUrl) 
  }
}
